<template>
	<div class="scrolldivinner">
	<div :id="item.name" v-for="(item, index) in list" class="colorwrap casecolors" @click="onclick(index, item)">
		<div class="centercolor">
			<div :class="item.active==1?'outercircle active':'outercircle'">
				<div class="circle circlecolor0" :style="item.style"> </div>
			</div><span>{{item.name}} </span>
		</div>
	</div>
</div>
</template>
<script>
import transparent from '../../assets/images/transparent.svg'
import { setActiveTrue } from '../../utils/common.js'

export default {
  name: 'Color',
  data() {
    return {
      list: [],
    }
  },
  props: {
    images: {

    }
  },
  watch: {
    images: function(colors){
      let item = {}
      let list = []
      for(let i in colors){
        item = {
            name: colors[i]['color'],
            style: 'background-color: '+ colors[i]['color'],
            active: (colors[i]['active']== 1 || i ==0 )?1:0
        }
        if(colors[i]['color'] == "Transparent") {
          item.style = "background-image: url("+ transparent +");"
        }
        
        list.push(item)
      }
      this.list = list
      if(this.list.length >0)this.onclick(0, this.list[0])
    }
  },
  created() {
    
  },
  mounted() {
    
  },
  methods: {
    onclick(i, e){
      this.list = setActiveTrue(i, this.list)
    	this.$emit('child-event', i, e)
    }
  }

};

</script>