<template>
	<div class="common-aside">
		<el-tabs :tab-position="tabPosition" style="" type="border-card" v-model="tabAsideHover" @tab-click="tabClick">
        <el-tab-pane >
          <div slot="label" class="el-tabs__item-label">
            <img src="../assets/images/product.png" width="50"/>
            <p>PRODUCT</p>
          </div>
          <div class="tab-product-main">
              <ul><li v-for="(item, index) in product" @click="product_click(item)">
                <el-image  v-if="item.icon !='' && item.icon && item.icon.indexOf('http')>=0" :src="item.icon" fit="fit"></el-image>
                <el-image  v-else :src="require('../assets/images/product0.png')" fit="fit"></el-image>
                <p>{{item.name}}</p>
              </li></ul>
          </div>
        </el-tab-pane>
        <el-tab-pane >
          <div slot="label" class="el-tabs__item-label">
            <img src="../assets/images/product_detail.png" width="50" />
            <p>PRODUCT DETAILS</p>
          </div>
          <div class="tab-product-main">
            <ul>
              <li v-for="(item, index) in product_detail_icon" :class="(item.active ==1)?'active':''">
                <div :class="item.class" @click="productDetailClick(index)"></div>
                <p>{{item.name}}</p>
              </li>
            </ul>
            <div class="tab-product-main-detail" v-show="product_detail_index == 0">
              <p class="step"><img src="../assets/images/1st.svg"><span>STEP: CHOOSE YOUR DEVICE'S BRAND BELOW</span></p>
              <el-select v-model="product_detail_band_value" @change="productDetailBandClick" ref="select"  filterable placeholder="请选择" @visible-change="productDetailBandVisibleChange">
                <el-option
                  v-for="item in product_detail_band_options"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id" >
                </el-option>
              </el-select>
            </div>
            <div class="tab-product-main-detail" v-show="product_detail_index == 1">
              <p class="step"><img src="../assets/images/2nd.svg"><span>STEP: CHOOSE YOUR DEVICE'S MODEL BELOW</span></p>
              <el-select v-model="product_detail_model_value" @change="productDetailModelClick" ref="selectModel"  filterable placeholder="请选择" @visible-change="productDetailModelVisibleChange">
                <el-option
                  v-for="item in product_detail_model_options"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>

            </div>
            <div class="tab-product-main-detail" v-show="product_detail_index == 2">
              <p class="step"><img src="../assets/images/3rd.svg"><span>STEP: Choose the type of case you want</span></p>
              <DetailCase :list="product_detail_case_list" @child-event="productDetailCaseClick"/>
            </div>
            <div class="tab-product-main-detail" v-show="product_detail_index == 3">
              <DetailColor :images="product_detail_case_info_colors" @child-event="productDetailColorClick"/>
            </div>

          </div>
        </el-tab-pane>
        <el-tab-pane >
          <div slot="label" class="el-tabs__item-label">
            <img src="../assets/images/icon4.png" width="50"/>
            <p>PICK A LAYOUT</p>
          </div>
          <Layout />
        </el-tab-pane>
        <el-tab-pane >
          <div slot="label" class="el-tabs__item-label">
            <img src="../assets/images/icon3.png"/>
            <p>UPLOAD A PICTURE</p>
          </div>
          <UploadIndex />
        </el-tab-pane>
        
        <el-tab-pane >
          <div slot="label" class="el-tabs__item-label">
            <img src="../assets/images/icon5.png"/>
            <p>TEXT&EFFECTS</p>
          </div>
          <TextCompent />
        </el-tab-pane>
      </el-tabs>
	</div>
</template>

<script>

import Layout from './layout/index.vue'
import TextCompent from './text.vue'
import UploadIndex from './upload/index.vue'
import Draw from '../components/detail/draw'
import DetailCase from '../components/detail/case'
import DetailColor from '../components/detail/color'
import { getProduct, getBand, getModel, getCase, getCaseInfo } from '../api/index.js'
import { product_detail_icon } from '../utils/gloabl.js'
import { setActiveTrue, setTabPosition, setCache } from '../utils/common.js'
export default {
	name: 'Aside',
  components: {
    DetailCase, DetailColor, Draw, Layout, UploadIndex, TextCompent
  },
  props: {
    tabPosition: {
    	type: String,
    	default: 'left'
    }
  },
  computed:{
    tabAsideHover: {
      get(){        
        return this.$store.state.tabAsideHover
      },
      set(val){
        this.$store.commit('SET_TABASIDEHOVER', val)
        // console.log(val)
      }
    }
  },
  data() {
    return {
      screenWidth: document.body.clientWidth,
      // tabAsideHover: 0,
      product_detail_icon,
      product_detail_index: 0,
      product_detail_band_options: [],
      product_detail_band_value: '',
      product_detail_model_options: [],
      product_detail_model_value: '',
      product_detail_case_list: [],
      product_detail_case_info: [],
      product_detail_case_info_colors: [],
      product: [],
      productSelect: {},
      productDetail: {
        category_id: 0,
        brand_id: 0,
        product_id: 0,
        type_id: 0,
        product: {},
        color: {}
      },
      
      
    };
  },
  created(){
    getProduct().then(res=> {
      this.product = res
      this.productSelect = res[0]
    })
    if(this.$route.query.product_id >0) {
      getCaseInfo(this.$route.query.product_id+ '&cate=1').then(res=>{
        this.$store.commit('SET_TABASIDEHOVER', '1')
        this.productDetailClick(3)
        this.productDetail.product = res
        this.saveInfoCache()

        this.product_detail_case_info = res
        if(res.diy.length == 0) {
          this.$message('参数错误，请重新选择产品')
        }else{
          this.product_detail_case_info_colors = res.diy
        }
        // if(res.cate) {
        //   this.product_click({name: '', id: res.cate.type_id})
        // }
      })
      //获取分类信息
      
    }
  	console.log(this.$route.query)
  },
  methods: {
    tabClick(e) {
      if(this.tab_hover == 1) {
        this.productDetailClick(0)
      }
      
    },
    product_click(item){
      this.productSelect = item
      this.productDetail.category_id = item.id
      this.productDetailClick(0)
      this.tabAsideHover = '1'
      this.$refs.select.toggleMenu()
    },
    productDetailClick(index){
      // if(this.product_detail_index == index)return;
      this.product_detail_index = index
      this.productDetailChangeHover(index);
      //左侧底部切换以及数据展示
      if(index == 0) {

        getBand(this.productSelect.id).then(res=> {

          this.product_detail_band_options = res
        })
      }
      
    },
    productDetailBandClick(e){
      this.product_detail_index = 1
      this.productDetailChangeHover(1)
      this.productDetail.brand_id = e
      getModel(e).then(res=> {
        this.product_detail_model_options = res
        this.$refs.selectModel.toggleMenu()
      })
    },
    productDetailBandVisibleChange(e){
      if(!e) {
        this.product_detail_index = 1
        this.productDetailChangeHover(1)
        this.$refs.selectModel.toggleMenu()
      }
    },
    productDetailModelClick(e){
      //获取改id,对应的产品id
      let product_detail_model_options = this.product_detail_model_options
      for (let i in product_detail_model_options) {
        if(product_detail_model_options[i]['id'] == e) {
          this.productDetail.product_id = product_detail_model_options[i]['product_id']
        }
      }
      
      this.product_detail_index = 2
      this.productDetailChangeHover(2)
      getCase(e).then(res=>{
        if(res.length == 0) {
          this.productDetailCaseClick(-1, {type_id:0, product_id: this.productDetail.product_id})
        }else{
          this.product_detail_case_list = res
        }
      })
    },
    productDetailModelVisibleChange(e){
      if(!e) {
        this.product_detail_index = 2
        this.productDetailChangeHover(2)
      }
    },
    productDetailCaseClick(i, e){
      this.productDetail.type_id = e.type_id
      this.productDetail.type = e
      if(i>=0)this.product_detail_case_list = setActiveTrue(i, this.product_detail_case_list)
      this.product_detail_index = 3
      this.productDetailChangeHover(3)
      getCaseInfo(e.id).then(res=>{
        this.productDetail.product = res
        this.saveInfoCache()

        this.product_detail_case_info = res
        if(res.diy.length == 0) {
          this.$message('参数错误，请重新选择产品')
        }else{
          this.product_detail_case_info_colors = res.diy
        }
      })
    },
    productDetailColorClick(i, e){//切换背景图片

      let row = this.product_detail_case_info_colors[i]
      this.$emit('child-event', row)
      this.productDetail.color = row
      this.saveInfoCache()
    },
    productDetailChangeHover(index){//产品详情改变选择的颜色背景
      let list = this.product_detail_icon
      let row = list[index]
      if(!row.hover) {
        row.class += ' hover'
        row.hover = true
      }
      list[index] = row
      this.product_detail_icon = setActiveTrue(index, list)
      this.productDetail.color = row
      this.saveInfoCache()
    },
    saveInfoCache(){
      this.$store.commit('SET_PRODUCTDETAILCASEINFO', this.productDetail)
    }
    
  },
  mounted () {
    const that = this
    
  }
};
</script>