import request from '@/utils/request'

export function uploadBase64(data) {
  return request({
    url: 'index.php?route=tool/upload64',
    method: 'post',
    data
  })
}
export function getProduct(data) {
  return request({
    url: 'index.php?route=api/category',
    method: 'get',
    data
  })
}

export function getBand(category) {
  return request({
    url: 'index.php?route=api/brand&category='+ category,
    method: 'get'
  })
}

export function getModel(id) {
  return request({
    url: 'index.php?route=api/model&brand=' +id,
    method: 'get'
  })
}

export function getCase(id) {
  return request({
    url: 'index.php?route=api/product/list&category=' + id,
    method: 'get'
  })
}

export function getCaseInfo(id) {
  return request({
    url: 'index.php?route=api/product&product_id=' + id,
    method: 'get'
  })
}
export function getBackground() {
  return request({
    url: 'index.php?route=api/background',
    method: 'get'
  })
}
export function getCaseInfoDefault(data) {
  return request({
    url: 'product/case/info/default',
    method: 'get',
    data
  })
}
