<template>
	<div class="layout text">
		<el-tabs @tab-click="handleClick">
			<el-tab-pane>
				<div slot="label" class="el-tabs__item-label">
					<img src="../assets/images/add-text.svg" />
					<p>ADD TEXT</p>
				</div>
				<p>YOUR TEXT</p>
				<el-input
				  type="textarea"
				  :rows="2"
				  placeholder=""
				  v-model="textarea">
				</el-input>
				<div id="desk_text_controls" class="textcontrolholder">
					<img class="bold" src="../assets/images/bold.svg" @click="setTextStyle(1)">
					<img class="italic" src="../assets/images/italic.svg" @click="setTextStyle(2)">
					<img class="align-center" src="../assets/images/align-center.svg" @click="setTextStyle(3)">
					<p id="add_new_text" class="addnewtext" @click="saveText">+ add more text</p>
				</div>
				<div class="box">
					<div class="box-li">
						<el-select v-model="fontFamily" placeholder="请选择">
					    <el-option
					      v-for="item in options"
					      :key="item"
					      :label="item"
					      :value="item">
					    </el-option>
					  </el-select>
					</div>
					<div class="box-li">
						<el-select v-model="fontSize" placeholder="请选择">
					    <el-option
					      v-for="item in options1"
					      :key="item"
					      :label="item"
					      :value="item">
					    </el-option>
					  </el-select>
					</div>
				</div>
				
				<Chrome v-model="colors" style="width: 100%" />

			</el-tab-pane>
			
		</el-tabs>
	</div>
</template>
<script>
import { Chrome } from 'vue-color'
import Konva from 'konva';

import Draw from '../utils/draw.js'
import { fonts } from '../utils/gloabl.js'
import { setActiveTrue, setTabPosition } from '../utils/common.js'
export default {
	name: 'TextCompent',
  components: {
  	Chrome
  },
  data() {
      return {
        dialogImageUrl: '',
        dialogVisible: false,
        // textarea: '',
        colors: '#194d33',
        fontSize: 56,
        options: ['Calibri', "Helvetica Neue", 'Helvetica',"PingFang SC","Microsoft YaHei", 'Arial', 'sans-serif', 'Allura', 'Cabin Sketch', 'Chango', 'Great Vibes', 'New Tegomin', 'Syne Mono', 'Train One', 'Monoton', 'Passion One'],
        options1: [6,8,9,10,11,12,14,18,24,30,46,48,56,60,72,90],
        fontFamily: 'Arial'

      };
  },
  watch: {
		colors(old, newcolor){
			let c = typeof old == 'object'?old.hex:old
			this.saveText()
		},
		textarea(n, o){
			this.saveText()
		},
		fontSize(n, o){
			this.saveText()
		},
		fontFamily(n, o){
			this.saveText()
		}
	},
	computed:{
//这里需要把store 动态的数据放到computed里面才会同步更新 视图
    textarea: {
    	get(){
    		return this.$store.state.textValue
    	},
    	set(e){
    		this.$store.commit('SET_TEXTVALUE', e)
    	}
    }
  },
  methods: {
  	handleRemove(file, fileList) {
        console.log(file, fileList);
      },
    handlePictureCardPreview(file) {
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
    },
    handleClick(index){

    },
    setTextStyle(type){
    	let activeNode = this.$store.state.activeNode
    	let text = this.$store.state.layer.findOne('#' + activeNode.getAttr('sid'))
    		if(!text)return
    	let fs = text.getAttr('fontStyle')
    	switch(type) {
    		case 1:
    		if(fs.indexOf('bold')>=0) {
    			text.setAttr('fontStyle', fs.replace('bold', ''))
    		}else{
    			text.setAttr('fontStyle', fs + ' bold')
    		}
    		break;
    		case 2:
    		if(fs.indexOf('italic')>=0) {
    			text.setAttr('fontStyle', fs.replace('normal', ''))
    		}else{
    			text.setAttr('fontStyle', fs + ' italic')
    		}
    		break
    		case 3:
    		if(text.getAttr('align') == 'center') {
    			text.setAttr('align', 'center')
    		}else{
    			text.setAttr('align', 'center')
    		}
    		break
    	}
    	this.$store.state.layer.draw()
    },
    saveText(){
    	let color = typeof this.colors == 'object'?this.colors.hex:this.colors
    	let store = this.$store
    	let activeNode = this.$store.state.activeNode
    	// console.log(activeNode.getAttr('name'))
    	if(activeNode.id == undefined || activeNode.getAttr('name') != 'uploadFontRect'){
    		let l = this.$store.state.layer.find('._text_').length
    		var simpleText = new Konva.Text({
	        x: store.state.background.nx/2,
	        y: store.state.background.nheight/2,
	        name: '_text_',
	        id: '_text_' + l,
	        text: this.textarea,
	        fontSize: this.fontSize,
	        fontFamily: this.fontFamily,
	        fill: color,
	      })
	      let greenRect = new Konva.Rect({
          id: 'uploadFontRect_' + l,
          name: 'uploadFontRect',
          x: store.state.background.nx*3/2,
          y: store.state.background.ny + store.state.background.nheight/2,
          width: simpleText.width(),
          height: simpleText.height(),
          draggable: true,
          sindex: l,
          sid: '_text_' + l,
        })
        
        Draw.resetBackgroundAndGridLine(this.$store)
        store.state.layer.findOne('#topGroup').add(simpleText)
        store.state.layer.add(greenRect)

        Draw.resetZIndex(store)
        store.state.transformer.nodes([greenRect])
        store.state.transformer.moveToTop()
        this.$store.state.layer.draw()
        this.$store.commit('SET_ACTIVENODE', greenRect)
        greenRect.moveToTop()
        greenRect.on('dragmove', function(e){
          store.state.layer.findOne('#_text_' + l).move({
            x: e.evt.movementX, 
            y: e.evt.movementY
          })
        })
        greenRect.on('dragend', function(e){
          //保存记录到缓存
          Draw.saveHistory(e, store)
        })
    	}else{
    		let text = this.$store.state.layer.findOne('#' + activeNode.getAttr('sid'))
    		if(!text)return
    		text.setAttr('text', this.textarea)
    		text.setAttr('fontSize', this.fontSize)
    		text.setAttr('fontFamily', this.fontFamily)
    		text.setAttr('fill', color)
    		activeNode.width(text.width())
    		activeNode.height(text.height())
    		
    		this.$store.state.layer.draw()
    		console.log(text.getAttr('fontFamily'))
    	}
    	
    }
  }
};
</script>