<template>
	<div class="edit-button">
		<div class="edit-button-item" v-for="(item, index) in getList" :style="item.placement=='left'?{left: item.left, top: item.top}:{right: item.left, top: item.top}">
			<img v-if="item.active == 1" src="../../assets/images/active-object.svg" class="editcontrol controlrect1" data-rect="rect1">
			<img v-else src="../../assets/images/inactive-object.svg" @click="clickSelect(index, item)" class="editcontrol controlrect1" data-rect="rect1">
    	
    	<el-popover
			  :placement="item.placement"
			  width="225"
			  trigger="click">
			  <div class="edit-button-color">
			  	<div class="change-color-title">CHANGE GRID COLOR</div>
			  	<Chrome v-model="colors" />
			  	<div class="no-bg"><el-checkbox @change="noBackground" v-model="item.noBg">no background</el-checkbox></div>
			  </div>
			  <div slot="reference" > <img src="../../assets/images/grid-color.svg"  class="colorcontrol colorrect1" v-if="item.active" @click="changeAcitveColor"></div>

			</el-popover>
		</div>
		
	</div>
</template>
<script>
import Konva from 'konva';
import { Chrome } from 'vue-color'

import { setActiveTrue } from '../../utils/common.js'
import  Draw from '../../utils/draw.js'
export default {
	components: {
		Chrome
	},
	name: 'editButton',
	data() {
		return {
			colors: '#194d33',
			list: []
		}
	},
	watch: {
		colors(old, newcolor){
			let c = typeof old == 'object'?old.hex:old
			this.changeAcitveColor(c)
		}
	},
	computed:{
//这里需要把store 动态的数据放到computed里面才会同步更新 视图
    getList(){
      return this.$store.state.editButtonList
    }
  },
	created(){
	},
	methods: {
		noBackground() {
			let grid_active = this.$store.state.explodeGridsActive
			if(!grid_active.active || grid_active.active == 0) {
				return
			}
			let idRect = 'uploadColorRect_' + grid_active.index
			this.$store.state.layer.findOne('#' + idRect).fill(null)
			this.$store.state.layer.draw()
		},
		clickSelect(index, item){
			Draw.showBackgroudUploadImage(index, this.$store)
		},
		changeAcitveColor(newcolor){
			let grid_active = this.$store.state.explodeGridsActive
    	// if(!grid_active.active || grid_active.active == 0) {
    	// 	this.$message({
     //      message: '请先点击编辑按钮选择区块',
     //      type: 'warning'
     //    });
    	// 	return
    	// }
    	let store = this.$store
			let idRect = 'uploadColorRect_' + grid_active.index
			let id_group = 'uploadGroup-' + grid_active.index
			let greenRect = store.state.layer.findOne('#'+idRect)
			if(greenRect) {
				// console.log(newcolor)
				greenRect.fill(newcolor)
				greenRect.getLayer().draw()
				//保存记录到缓存
      	Draw.saveHistory(greenRect, store)
				return
			}
			greenRect = new Konva.Rect({
          id: idRect,
          name: 'uploadColorRect',
          x: 0,
          y: 0,
          width: grid_active.width,
          height: grid_active.height,
          fill: this.colors,
          clipX: 0,
          clipY: 0,
          clipWidth: grid_active.width,
          clipHeight: grid_active.height,
        })
			greenRect.setAttr('sindex', grid_active.index)
			store.state.layer.findOne('#' + id_group ).add(greenRect)
      Draw.resetBackgroundAndGridLine(store)
      greenRect.moveToBottom()
      Draw.resetZIndex(store)
      //保存记录到缓存
      Draw.saveHistory(greenRect, store)
		}
	}
};
</script>