<template>
	<div class="draw-main" id="draw">
	<v-stage ref="stage" :config="stageSize" @mousedown="handleStageMouseDown" @touchstart="handleStageMouseDown" >
    <v-layer ref="layer">
      <v-transformer ref="transformer" :config="transformerConfig" @transform="handleTransformEnd"/>
    </v-layer>
  </v-stage>
  <div v-for="(item, index) in getList" v-show="preview" :class="(item.active == 1)?'ui-flex justify-center center explode-grid-item active':'ui-flex justify-center center explode-grid-item'" :style="item.style">
      <div class="cell" v-show="item.uploadicon"><img class="addimage" src="../../assets/images/upload-image.svg" @click.stop="uploadImageHandleClick(item)"></div>
  </div>
  <div class="draw-left-edit" v-show="preview">
    <EditButton v-show="actionIcon" />
  </div>
  <div class="draw-right" v-show="actionIcon" :style="{left: drawRightLeft}">
      <img @click="clickHandAction(1)" class="zoomin" title="zoomin" src="../../assets/images/zoom-in.svg" v-show="preview">
      <img @click="clickHandAction(2)" class="zoomout" title="zoomout" src="../../assets/images/zoom-out.svg" v-show="preview">
      <img @click="clickHandAction(3)" class="rotateright" title="rotate right" src="../../assets/images/rotate-right.svg" v-show="preview">
      <img @click="clickHandAction(4)" class="rotateleft" title="rotate left" src="../../assets/images/rotate-left.svg" v-show="preview">
      <div @click="handleUndoRedo(1)" class="controlundo bottomicon hide" v-show="preview">
        <p>undo</p>
      </div>
      <div @click="handleUndoRedo(2)" class="controlredo bottomicon righticonbottom "  v-show="preview">
        <p>redo</p>
      </div>
      <div class="controldelete righticon righticonbottom" @click="removeAll(0)" style="margin-top: 20px"  v-show="preview">
        <div class="controliconholder"><img width="30"  class="zoomin" title="zoomin" src="../../assets/images/delete.svg"></div>
        <p>item</p>
      </div>

      <div class="clearall righticon righticonbottom" @click="removeAll(1)" v-show="preview">
        <div class="controliconholder"><img width="30"  class="zoomin" title="zoomin" src="../../assets/images/delete-red.svg"></div>
        <p>all</p>
      </div>
      <div class="previewmode righticon righticonbottom" @click="clickPreview">
        <div class="controliconholder"><img  width="50" class="zoomin" title="zoomin" src="../../assets/images/preview.svg" ></div>
        <p>preview</p>
      </div>
  </div>

	</div>
</template>
<script>
import Konva from 'konva';

import logo from '@/assets/images/phone.png'
import draw from '../../utils/draw.js'
import { getCaseInfoDefault } from '../../api/index.js'
import { getCache, setCache } from '../../utils/common.js'
import EditButton from './editButton'
export default {
  components: {
    EditButton
  },
	name: 'Draw',
	data() {
		return {
      actionIcon: false,
      historyStep: 0,//撤销步骤
      history: [],
      preview: true,//不是预览模式
			stageSize: {
        width: 11,
        height: 800,
        clearBeforeDraw: true
      },
      layer: {},
      transformerConfig: {
        id: 'transformer',
        anchorStroke: 'red',
        anchorFill: 'yellow',
        anchorSize: 10,
        borderStroke: 'green',
        borderDash: [3, 3],
      },
      drawRightLeft: '200px'
		}
	},
  computed:{
//这里需要把store 动态的数据放到computed里面才会同步更新 视图
    getList: {
      get(){
        var list = this.$store.state.explodeGrids
        // console.log(list)
        for (let i in list) {
          list[i]['active'] = 0
          let style = {left: list[i]['left'] + 'px', top: list[i]['top'] + 'px', width: (list[i]['width']) + 'px', height: (list[i]['height']) + 'px'}
          if(list[i]['border'] == 'Circle') {
            style.borderRadius = '50%'
          }
          list[i]['style'] = style
        }
        this.$store.commit('SET_EXPLODEGRID', list)
        return list
      }
    }
  },
	created(){
  },
  mounted(){
  	
    var draw = document.getElementById("draw")
    this.stageSize.width = draw.clientWidth
    // this.stageSize.width = draw.clientWidth
    this.init()
  },
	methods: {
		init(){
			var complexText = new Konva.Text({
        x: 20,
        y: 60,
        text:
          "请点击选择产品",
        fontSize: 18,
        fontFamily: 'Calibri',
        fill: '#555',
        width: 300,
        padding: 20,
        align: 'center',
        id: 'startText'
      });
      this.$refs.layer.getNode().add(complexText)

      this.layer = this.$refs.layer.getNode()
      // this.$refs.stage.getNode().scaleX(1)
      this.$store.commit('SET_LAYER', this.layer)
      this.$store.commit('SET_TRANSFORMER', this.$refs.transformer.getNode())
      this.$store.commit('SET_STAGE', this.$refs.stage.getNode())
      let that = this


//默认商品
      let default_phone = getCache('__changeBackground__')
      if(!default_phone) {
        // getCaseInfoDefault().then(res=>{
        //   this.changeBackground(res)
        // })
      }else{
        this.changeBackground(default_phone)
      }

		},
		changeBackground(e){
      console.log(e)
      //改变背景的时候清除之前的背景图片
      if(this.layer.findOne('#startText'))this.layer.findOne('#startText').destroy()
      if(this.layer.findOne('#background'))this.layer.findOne('#background').destroy()
			if(this.layer.findOne('#tbackground'))this.layer.findOne('#tbackground').destroy()
      this.layer.getChildren(function(node){
        if(node.getClassName() != 'Transformer') {
          node.destroy()
        }
      })
      
      //装载图片
			let sources = {
        background: e.src_url,
        tbackground: e.src_overlay_url,
      };
      console.log(sources)
      let that = this
      let loading = this.$loading({
          lock: true,
          text: 'Loading',
      })
			draw.loadImages(sources, function(images){
        loading.close()
        let size = {
          width: images.tbackground.width,
          height: images.tbackground.height,
        }
        draw.setBackgroundSize(size, that.$store, e)
        that.$store.commit('SET_BGIMAGE', images.tbackground)
        //设置右侧工具条位置
        that.drawRightLeft = (that.$store.state.background.x + that.$store.state.background.width + 50) + 'px'

				let background = new Konva.Image({
          id: 'background',
          x: that.$store.state.background.x,
          y: that.$store.state.background.y,
          image: images.background,
          width: that.$store.state.background.width,
          height: that.$store.state.background.height,
        })
        background.on('click', function (e) {
          // draw.setEditButton(that.$store, that.$store.state.explodeGrids.length)
        })

        
        draw.setGroup(that.$store)
       
        that.layer.add(background)
        that.layer.batchDraw()
        that.actionIcon = true

        //设置html 编辑按钮以及透明背景和上传按钮
        draw.setEditButton(that.$store, 1)

        setCache('__changeBackground__', e)

        draw.saveHistory(e, that.$store)

			}, loading)
      
		},
    removeAll(type){//删除节点
      let group = this.$store.state.group;
      if(type == 1) {
        //清除之前的线和圈
        draw.removeNodeByName('._line_')
        draw.removeNodeByName('._group_')
        draw.removeNodeByName('._text_')
        draw.removeNodeByName('.uploadGroup')
        draw.removeNodeByName('.uploadFontRect')
        
        draw.setEditButton(this.$store, 1)
      }else{
        let activeNode = this.$store.state.activeNode
        if(typeof activeNode.id == 'undefined'){
          return
        }
        //以下是放大缩小旋转
        let sindex = activeNode.getAttr('sindex')
        let sid = activeNode.getAttr('sid')
        if(sindex == undefined || sid == undefined) {//只有当绑定了对应的层，才会执行放大缩小 旋转
          return
        }
        let node = this.$store.state.layer.findOne('#' + sid)
        if(node == undefined) {//找到对应的元素
          return
        }
        activeNode.remove()
        node.remove()
      }
      draw.resetBackgroundAndGridLine(this.$store)
      //设置upload icon 是否显示
      draw.setUploadIconIsShow(this.$store)
      this.$store.state.transformer.nodes([])
      // this.layer.add(group)
      this.layer.batchDraw()
    },
    uploadImageHandleClick(item){
      document.querySelector('input[name=file]').click()
      this.$store.commit('SET_TABASIDEHOVER', '3')
    },
    // handkeHideClickBackgroud(){//设置背景点击后隐藏
    //   this.$store.state.transformer.moveToTop()
    //   draw.hideBackgroudUploadImage(this.$store)
    // },
    handleUndoRedo(type){
      // return
      let history = this.$store.state.history
      let historyStep = this.$store.state.historyStep
      let list
      console.log(historyStep)
      console.log(history)
      if(type == 1) {//撤销
        if (historyStep <= 0) {
          list = history[0]
        }else{
          historyStep -= 1;
          list = history[historyStep-1]
        }
        
      }else{//前进
        let default_phone = getCache('__changeBackground__')
        if(!default_phone) {
          // getCaseInfoDefault().then(res=>{
          //   this.changeBackground(res)
          // })
        }else{
          this.changeBackground(default_phone)
        }
        return
        if (historyStep == history.length - 1) {
          return;
        }
        historyStep += 1;
        list = history[historyStep]
      }
      this.$store.commit('SET_HISTORYSTEP', historyStep)

      draw.updateStageByJson(list)

      this.layer.batchDraw()

      return
      
    },
    clickHandAction(type){

      let activeNode = this.$store.state.activeNode
      if(typeof activeNode.id == 'undefined'){
        return
      }
      
      //以下是放大缩小旋转
      let sindex = activeNode.getAttr('sindex')
      let sid = activeNode.getAttr('sid')
      if(sindex == undefined || sid == undefined) {//只有当绑定了对应的层，才会执行放大缩小 旋转
        return
      }
      let node = this.$store.state.layer.findOne('#' + sid)
      if(node == undefined) {//找到对应的元素
        return
      }
      switch(type){
        case 1:
        activeNode.width(activeNode.width()+3)
        activeNode.height(activeNode.height()+3)
        node.width(activeNode.width()+3)
        node.height(activeNode.height()+3)
        break;
        case 2:
        activeNode.width(activeNode.width()-3)
        activeNode.height(activeNode.height()-3)
        node.width(activeNode.width()-3)
        node.height(activeNode.height()-3)
        break;
        case 3:
        activeNode.rotation(activeNode.rotation()+45)
        node.rotation(node.rotation()+45)
        break;
        case 4:
        activeNode.rotation(activeNode.rotation()-45)
        node.rotation(node.rotation()-45)
        break;
      }

      this.layer.batchDraw()
      
    },
		handleTransformEnd(e) {//剪切图片和旋转的时候 设置底部图片
      //保存记录到缓存
      draw.saveHistory(e, this.$store)
      //获取当前选中的图片
      // let image = this.layer.findOne('#uploadImage-' + this.$store.state.transformerImageIndex)
      let image = this.layer.findOne('#' + e.target.getAttr('sid'))
      let x = e.target.x()
      let y = e.target.y()
      if(e.target.getAttr('sid').indexOf('text') >=0) {
        x -= this.$store.state.background.nx
        y -= this.$store.state.background.ny
      }
      image.width(e.target.width())
      image.height(e.target.height())
      image.rotation(e.target.rotation())
      image.scaleX(e.target.scaleX())
      image.scaleY(e.target.scaleY())
      // image.cornerRadius(e.target.width())
      image.x(x)
      image.y(y)
      this.layer.batchDraw()
      console.log(x)
      return 
      // shape is transformed, let us save new attrs back to the node
      // find element in our state
      const rect = this.rectangles.find(
        (r) => r.name === this.selectedShapeName
      );
      // update the state
      rect.x = e.target.x();
      rect.y = e.target.y();
      rect.rotation = e.target.rotation();
      rect.scaleX = e.target.scaleX();
      rect.scaleY = e.target.scaleY();

      // change fill
      rect.fill = Konva.Util.getRandomColor();
    },
    handleStageMouseDown(e) {
      // this.$store.state.layer.getChildren(function(a){
      //   console.log(a)
      // })
      //设置背景点击事件
      if(e.target.id() == "background" || e.target.id() == "tbackground") {
        this.$store.state.transformer.moveToTop()
        draw.hideBackgroudUploadImage(this.$store)
      }
      
      if (e.target === e.target.getStage()) {//点击空白处
        draw.hideBackgroudUploadImage(this.$store)
        return;
      }
      this.$store.commit('SET_ACTIVENODE' ,e.target)
      

      // return 
      // clicked on stage - clear selection
      if (e.target === e.target.getStage() || e.target.id() == "topGroupBgRect") {
        this.selectedShapeName = '';
        this.updateTransformer();
        return;
      }

      //分割当前点的rect ,设置active
      if(e.target.id().indexOf('uploadImageRect') >= 0 || e.target.id().indexOf('uploadFontRect')>=0) {
        let index = e.target.getAttr('sindex')
        this.$store.commit('SET_TRANSFORMERIMAGEINDEX', index)
        this.$store.state.transformer.moveToTop()
        draw.hideBackgroudUploadImage(this.$store)
      }
      //x选择中text文字,设置左侧文本内容
      if(e.target.id().indexOf('uploadFontRect')>=0) {
        let textSelected = this.layer.findOne('#' + e.target.getAttr('sid'))
        console.log(textSelected)
        if(textSelected) {
          this.$store.commit('SET_TABASIDEHOVER', '4')
          this.$store.commit('SET_TEXTVALUE', textSelected.getAttr('text'))
          draw.hideBackgroudUploadImage(this.$store)
          // this.tabAsideHover = '3'
        }
      }

      // clicked on transformer - do nothing
      const clickedOnTransformer =
        e.target.getParent().className === 'Transformer';
      if (clickedOnTransformer) {
        return;
      }



      // find clicked rect by its name
      const transformerNode = this.$refs.transformer.getNode();
      const stage = transformerNode.getStage();
      if(e.target.id().indexOf('upload') <0 && e.target.id().indexOf('Rect') <0) {
        transformerNode.nodes([]);
        // console.log(transformerNode)
        if(transformerNode.getLayer())transformerNode.getLayer().batchDraw();
        return
      }
      
      const selectedNode = stage.findOne('#' + e.target.id())



      if (selectedNode === transformerNode.node()) {
        return;
      }
      if (selectedNode) {
        // attach to another node
        transformerNode.nodes([selectedNode]);
      } else {
        // remove transformer
        transformerNode.nodes([]);
      }
      transformerNode.getLayer().batchDraw();


      return


      const name = e.target.name();
      this.selectedShapeName = name
      // const rect = this.rectangles.find((r) => r.name === name);
      // if (rect) {
      //   this.selectedShapeName = name;
      // } else {
      //   this.selectedShapeName = '';
      // }
      this.updateTransformer();
    },
    clickPreview(){
      this.preview = !this.preview
      const transformerNode = this.$refs.transformer.getNode();
      transformerNode.nodes([]);
      transformerNode.getLayer().batchDraw();
    },
    updateTransformer() {
      // here we need to manually attach or detach Transformer node
      const transformerNode = this.$refs.transformer.getNode();
      const stage = transformerNode.getStage();
      const { selectedShapeName } = this;

      const selectedNode = stage.findOne('.' + selectedShapeName);
      // do nothing if selected node is already attached
      if (selectedNode === transformerNode.node()) {
        return;
      }

      if (selectedNode) {
        // attach to another node
        transformerNode.nodes([selectedNode]);
      } else {
        // remove transformer
        transformerNode.nodes([]);
      }
      transformerNode.getLayer().batchDraw();
    }
	}
};
</script>