<template>
  <div :class="'index-page ' + tabPosition">

    <div class="el-page-header"  style="
        height: 42px;
        line-height: 42px;margin: 16px;
    "><div class="el-page-header__left" style="font-size: 32px;" @click="goBack"><i class="el-icon-s-home"></i><div class="el-page-header__title" style="font-size: 18px;">Home</div></div></div>


    <el-row :gutter="10">
      <el-col :xs="elRow" :sm="elRow" :md="elRow0" :lg="elRow0" :xl="elRow0" class="main-one">
        <div class="grid-content bg-purple" v-if="tabPosition == 'left'">
          <Aside :tabPosition="tabPosition"  @child-event="asideProductDetailColorClick" />
        </div>
        <Draw ref="draw" v-else  />
      </el-col>
      <el-col :xs="elRow" id="main-two" :sm="elRow" :md="elRow1" :lg="elRow1" :xl="elRow1" class="main-two">
        <div class="grid-content bg-purple" v-if="tabPosition == 'bottom'">
          <Aside :tabPosition="tabPosition"  @child-event="asideProductDetailColorClick" />
        </div>
        <Draw ref="draw" v-else  />
      </el-col>
      <el-col :xs="elRow" :sm="elRow" :md="elRow2" :lg="elRow2" :xl="elRow2" class="main-three">
          <div class="grid-content bg-purple result-main" :style="{height: screenHeight+'px'}" v-if="productInfo.hasOwnProperty('product')">
            <div class="main-price" >
              <h3>{{productInfo.product.name}}</h3>
              <h3 v-if="productInfo.type && productInfo.type.hasOwnProperty('name')" v-show="false">{{productInfo.type.name}}</h3>
              <h2>{{productInfo.product.price}}</h2>
              <div class="add-cart" @click="addCart"><img src="../assets/images/add-to-cart.svg"></div>
            </div>
            <el-button type="primary" plain @click="saveImage" v-show="false">导出</el-button>
          </div>
      </el-col>
    </el-row>

    <el-dialog :visible.sync="dialogFormVisible">
      <el-tabs v-model="activeName">
        <el-tab-pane label="Login" name="first">
          <el-form :model="form" :rules="rules" ref="ruleForm">
            <el-form-item label="USERNAME" prop="username">
              <el-input v-model="form.username" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="PASSWORD" prop="passowrd">
              <el-input v-model="form.passowrd" type="password"></el-input>
            </el-form-item>
          </el-form>
        </el-tab-pane>
        <el-tab-pane label="Register" name="second">
          <el-form :model="form" :rules="rulesr" ref="ruleFormR">
            <el-form-item label="First Name" prop="firstname">
              <el-input v-model="form.firstname" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="Last Name" prop="lastname">
              <el-input v-model="form.lastname" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="E-Mail" prop="email">
              <el-input v-model="form.email" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="Telephone" prop="telephone">
              <el-input v-model="form.telephone" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="PASSWORD" prop="password">
              <el-input v-model="form.password" type="password"></el-input>
            </el-form-item>
            <el-form-item label="Password Confirm" prop="confirm">
              <el-input v-model="form.confirm" type="password"></el-input>
            </el-form-item>
          </el-form>
        </el-tab-pane>
      </el-tabs>
      
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">CANCEL</el-button>
        <el-button type="primary" @click="loginForm">OK</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>

import Draw from '../components/detail/draw'
import Aside from '../components/aside.vue'
import { product_detail_icon } from '../utils/gloabl.js'
import { setActiveTrue, setTabPosition, downloadURI } from '../utils/common.js'
import draw from '../utils/draw.js'
import { getToken, setCache } from '@/utils/common'
import { setToken } from '@/utils/auth'
import { login, addPostCart, register } from '@/api/auth'
import { uploadBase64 } from '@/api/index'

export default {
  components: {
    Aside, Draw
  },
  data() {
    return {
      activeName: 'first',
      elRow: {span: 24, offset: 0},
      elRow0: {span: 8, offset: 0},
      elRow1: {span: 12, offset: 0},
      elRow2: {span: 4, offset: 0},
      tabPosition: 'left',
      screenWidth: document.body.clientWidth,
      screenHeight: document.body.clientHeight,
      tab_hover: 0,
      product_detail_icon,
      product_detail_index: 0,
      product_detail_band_options: [],
      product_detail_band_value: '',
      product_detail_model_options: [],
      product_detail_model_value: '',
      product_detail_case_list: [],
      product_detail_case_info: [],
      product_detail_case_info_colors: [],
      product: [],
      dialogFormVisible: false,
      formLabelWidth: '200px',
      form: {
        username: '',
        passowrd: ''
      },
      rules: {
          username: [
            { required: true, trigger: 'blur' }
          ],
          passowrd: [
            { required: true, trigger: 'blur' }
          ]
          
      },
      formr: {
        firstname: '',
        lastname: '',
        email: '',
        telephone: '',
        passowrd: ''
      },
      rulesr: {
          firstname: [
            { required: true, trigger: 'blur' }
          ],
          lastname: [
            { required: true, trigger: 'blur' }
          ],
          email: [
            { required: true, trigger: 'blur' }
          ],
          telephone: [
            { required: true, trigger: 'blur' }
          ],
          passowrd: [
            { required: true, trigger: 'blur' }
          ]
          
      }

      
    };
  },
  created(){
  	
  },
  methods: {
    asideProductDetailColorClick(row){
      this.$refs.draw.changeBackground(row);
    },
    saveImage(){
      var dataURL = this.$refs.draw.$refs.stage.getNode().toDataURL({ pixelRatio: 4 });
      downloadURI(dataURL, 'stage.png');
    },
    addCart() {
      let transformer = this.$store.state.layer.findOne('#transformer')
      if(transformer) {
        transformer.nodes([])
        transformer.getLayer().batchDraw()
      }
      let token = getToken()
      if(!token) {
        this.dialogFormVisible = true
        return
      }
      //导出图片，增加到购物车
      this.cartDo()
    },
    cartDo(){
      var dataURL = this.$refs.draw.$refs.stage.getNode().toDataURL({ pixelRatio: 4 });
      let loading = this.$loading({
          lock: true,
          text: 'Loading',
      })
      uploadBase64({image64: dataURL}).then(res=> {
        
        if(res.file) {
          let data = this.$store.state.productDetailCaseInfo
          data.file = res.file
          data.quantity = 1
          addPostCart(data).then(res=> {
            loading.close()
            this.$message(res.message);
          })
        }else{
          this.$message({
                  message: 'error',
                  type: 'warning'
                });
        }
        
      })

    },
    loginForm(){
      if(this.activeName != 'first') {
        this.$refs['ruleFormR'].validate((valid) => {
            if (valid) {
              this.form.key = this.form.passowrd
              this.form.type = 'api'
              this.form.newsletter = 0
              this.form.agree = 1
              register(this.form).then(res=> {
                if(res.token) {
                  setToken(res.token)
                  this.dialogFormVisible = false
                  this.$message({
                    message: 'success',
                    type: 'success'
                  });
                }else{
                  this.$message({
                    message: 'error',
                    type: 'warning'
                  });
                }
              })
            } else {
              console.log('error submit!!');
              return false;
            }
        });
        return
      }
      this.$refs['ruleForm'].validate((valid) => {
          if (valid) {
            this.form.key = this.form.passowrd
            login(this.form).then(res=> {
              if(res.token) {
                setToken(res.token)
                this.dialogFormVisible = false
                this.$message({
                    message: 'success',
                    type: 'success'
                });
              }else{
                this.$message({
                  message: 'error',
                  type: 'warning'
                });
              }
            })
          } else {
            console.log('error submit!!');
            return false;
          }
      });
    },
    goBack() {
      window.location.href="/"
    }
    
  },
  computed:{
//这里需要把store 动态的数据放到computed里面才会同步更新 视图
    productInfo: {
      get(){
        return this.$store.state.productDetailCaseInfo
      }
    }
  },
  mounted () {
    const that = this
    this.screenHeight = document.body.clientHeight
    setTabPosition(that)
    window.onresize = () => {
      setTabPosition(that)
      let imageSize = that.$store.state.stage.findOne('#tbackground')
      if(imageSize) {
        draw.setBackgroundSize(imageSize.size(), that.$store)
      }
      //设置宽度
      that.$refs.draw.$refs.stage.getNode().width(document.body.clientWidth)
      return (() => {
            window.screenWidth = document.body.clientWidth
            that.screenWidth = window.screenWidth
      })()
    }
  }
};
</script>
