					idRect = 'uploadGroup-' + (i-1)
					arrImages[idRect] = item.inthumb[i]
					
				}
				Draw.loadImages(arrImages, function(res){
					loading.close()

					let group


					let groups = that.$store.state.layer.find('._group_')
					for(let ii in res){
						group = that.$store.state.layer.findOne('#'+ ii)
						let index = (ii.replace('uploadGroup-', ''))
						if(!group){continue}
						let image = new Konva.Image({
		              id: 'uploadImage-' + index,
		              name: 'uploadImage',
		              x: 0,
		              y: 0,
		              image: res[ii],
		              width: group.width(),
		              height: group.height(),
		              draggable: true,
		        })
						group.add(image)
						let greenRect = new Konva.Rect({
		          id: 'uploadImageRect-' + index,
		          name: 'uploadImageRect',
		          x: 0,
		          y: 0,
		          width: group.width(),
		          height: group.height(),
		          draggable: true,
		          sindex: index,
		          sid: 'uploadImage-'+index
		        })
		        greenRect.on('dragmove', function(e){
          
		          that.$store.state.layer.findOne('#'+'uploadImage-' + index).move({
		            x: e.evt.movementX, 
		            y: e.evt.movementY
		          })
		        })
		        greenRect.on('dragend', function(e){
		          //保存记录到缓存
		          Draw.saveHistory(e, that.$store)
		        })

		        groups.each(function(e, i){
		          if(index == i) {
		            e.add(greenRect)
		            e.moveToTop()
		          }else{
		            e.zIndex(that.$store.state.layer.getChildren().length - 1)
		          }
		        })
					}
      		Draw.setUploadIconIsShow(that.$store)

					Draw.resetBackgroundAndGridLine(that.$store)
		  		Draw.resetZIndex(that.$store)
	        that.$store.state.layer.draw()
				}, loading)
			}

			if(!item.background) {
