<template>
  <div class="upload">
    <el-upload ref="uploadref" action="/index.php?route=tool/userupload&uid=1" list-type="picture-card" :on-preview="handlePictureCardPreview" :show-file-list="false" :on-remove="handleRemove" :on-success="uploadSuccess">
      <i class="el-icon-plus"></i>
    </el-upload>
    <div style="margin-top:20px">
      <ul class="el-upload-list el-upload-list--picture-card">
        <li v-for="(item,index) in upload_list" @click="handlePictureCardPreview(item)" class="el-upload-list__item is-success">
          <a href="javascript:;"><img :src="item.url" alt="" class="el-upload-list__item-thumbnail"></a>
        </li>
      </ul>
    </div>
    <el-dialog :visible.sync="dialogVisible">
      <img width="100%" :src="dialogImageUrl" alt="">
    </el-dialog>
  </div>
</template>
<script>
import Konva from 'konva';
import Draw from '../../utils/draw.js'
import { setActiveTrue, setTabPosition } from '../../utils/common.js'
export default {
  name: 'UploadIndex',
  components: {},
  data() {
    return {
      dialogImageUrl: '',
      dialogVisible: false
    };
  },
  computed: {
    upload_list: {
      get() {
        let list = this.$store.state.uploadList
        if (list.length > 0) {
          for (let i in list) {
            if (!list[i]['response']) continue;
            list[i]['url'] = list[i]['response']['data']['file']
          }
        }
        return list
      },
      set(val) {}
    },
    openFile: {
      get() {}
    }
  },
  watch: {
    openFile(val) {

    }
  },
  mounted() {


  },
  methods: {
    handleRemove(file, fileList) {
      this.$store.commit('SET_UPLOADLIST', fileList)
    },
    handlePictureCardPreview(file) {
      let grid_active = this.$store.state.explodeGridsActive
      // if(!grid_active.active || grid_active.active == 0) {
      //  this.$message({
      //      message: '请先点击编辑按钮选择区块',
      //      type: 'warning'
      //    });
      //  return
      // }
      
      console.log(file)
      let store = this.$store
      let id = 'uploadImage-' + grid_active.index;
      let idRect = 'uploadImageRect-' + grid_active.index;
      let id_group = 'uploadGroup-' + grid_active.index;

      //删除之前存在的
      Draw.removeNodeById('#' + id)
      Draw.removeNodeById('#' + id + '_Circle')
      Draw.removeNodeById('#' + idRect)
      

      if (store.state.layer.findOne('#' + id)) {
        return
      }
      let saveGroupImage = store.state.layer.findOne('#' + id_group)
      let loading = this.$loading({
        lock: true,
        text: 'Loading',
      })
      //将图片加入到图层里面
      Draw.loadImages({ uploadImage: file.url }, function(res) {
        loading.close()
        // let width = res.uploadImage.width
        // let height = res.uploadImage.height
        
        let height = saveGroupImage.height()
        let width = (saveGroupImage.height()/res.uploadImage.height) * res.uploadImage.width
        
        let uploadImage

        //选择的图片
        uploadImage = new Konva.Image({
          id: id,
          name: 'uploadImage',
          x: -width/2 + saveGroupImage.width()/2,
          y: 0,
          image: res.uploadImage,
          width: width,
          height: height,
          draggable: true,
        })
        saveGroupImage.add(uploadImage)

        if (store.state.layoutActive == 6) { //当是圆形的时候，填充背景的方式
          let uploadImageCircle = new Konva.Circle({
            id: id + '_Circle',
            name: 'uploadImage',
            x: width / 2,
            y: width / 2,
            radius: width,
            width: width,
            height: height,
            draggable: true,
            // fill: 'red',
          })
          saveGroupImage.add(uploadImageCircle)
          uploadImageCircle.moveToTop()
        }


        //最外层方框，用于点击拖拽事件
        let greenRect = new Konva.Rect({
          id: idRect,
          name: 'uploadImageRect',
          x: -width/2 + saveGroupImage.width()/2,
          y: 0,
          width: width,
          height: height,
          draggable: true,
          sindex: grid_active.index,
          sid: id
        })


        Draw.resetBackgroundAndGridLine(store)

        // tr = tr.nodes([uploadImage])

        // tr.getLayer().batchDraw();
        // 设置层级_group_为最大，transformer仅次
        let groups = store.state.layer.find('._group_')
        groups.each(function(e, i) {
          if (grid_active.index == i) {
            e.add(greenRect)
            e.moveToTop()
          } else {
            e.zIndex(store.state.layer.getChildren().length - 1)
          }
        })
        Draw.resetZIndex(store)
        store.state.transformer.zIndex(store.state.layer.getChildren().length - groups.length)
        // store.state.transformer.anchorSize(30)

        store.state.layer.draw()

        //保存记录到缓存
        // Draw.saveHistory(uploadImage, store)

        greenRect.on('dragmove', function(e) {

          store.state.layer.findOne('#' + id).move({
            x: e.evt.movementX,
            y: e.evt.movementY
          })
        })
        greenRect.on('dragend', function(e) {
          //保存记录到缓存
          Draw.saveHistory(e, store)
        })
        //设置upload icon 是否显示
        Draw.setUploadIconIsShow(store)

      }, loading)
      //设置隐藏背景颜色
      Draw.hideBackgroudUploadImage(this.$store)
    },
    uploadSuccess(response, file, fileList) {
      // console.log(fileList)
      // //删除之前的图片，替换
      // let grid_active = this.$store.state.explodeGridsActive
      // let id = 'uploadImage-' + grid_active.index
      // let idRect = 'uploadImageRect-' + grid_active.index;

      

      this.handlePictureCardPreview(file)
      this.$store.commit('SET_UPLOADLIST', fileList)
    }
  }
};
</script>